/*defined variable*/
$colorPrimary: #222;
$colorFont: #5AC7C7;
$colorFontDark: rgb(27, 150, 150);
$colorWhite: #fff;
$colorTranslucent: rgba(0, 0, 0, 0.6);

.ios .navbar .logo {
    left: auto !important;
}

.select-title {
    background-image: url('../assets/select/bg.png');
    background-repeat: repeat-x;
    background-size: contain;
    background-position: center;
    padding: 5px 0;
    text-align: center;
    letter-spacing: 2px;
    font-size: 0.938em;
    font-weight: 600;
    color: $colorPrimary;
    border-bottom: 1px solid $colorPrimary;
}

.select {
    top: 74px;
    bottom: 0;
    height: auto;

    ul,
    li {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    ul {
        margin-top: -44px;
    }
}

.select-item {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 300px;
    cursor: pointer;

    .select-item-block {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: row;
        padding: 8px 10px;
        background: $colorTranslucent;
        color: $colorWhite;

        .content {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            padding-right: 10px;
            word-break: break-all;

            h1 {
                margin: 0 0 2px;
                font-size: 1em;
                letter-spacing: 1px;
            }

            p {
                margin: 0;
                font-size: 0.938em;
            }
        }

        .buttons {
            display: flex;
            flex-direction: column;
            -webkit-align-items: center;
            align-items: center;
            -webkit-justify-content: center;
            justify-content: center;

            .link {
                width: 30px;
                color: $colorWhite;

                .f7-icons {
                    font-size: 1.75em;
                }

                &:hover {
                    color: $colorFontDark;
                }
            }
        }
    }
}


@media screen and (max-width: 450px) {
    .select-item {
        height: 190px;
    }
}